 * 全局Reset
 * 重置Mobile及高级浏览器上常见的差异
 */
*,
::before,
::after {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
html,
body {
  overflow: hidden;
  height: 100%;
}
::-webkit-scrollbar {
  display: none;
}
html {
  background-color: #ffffff;
  color: #333;
  font-size: 100px;
  -webkit-user-select: none;
  -moz-user-select: none;
   -ms-user-select: none;
       user-select: none;
}
body {
  margin: 0;
  font-size: 0.14em;
  line-height: 1.5;
