blockquote:before,
blockquote:after,
q:before,
q:after {
  content: " ";
}
textarea,
pre,
xmp {
  overflow: auto;
  -webkit-overflow-scrolling: touch;
}
textarea {
  resize: vertical;
}
input,
textarea,
button,
select
a {
  outline: 0 none;
}
input,
textarea,
button,
select {
  color: inherit;
}
input:disabled,
  textarea:disabled,
  button:disabled,
  select:disabled {
    opacity: 1;
}
button::-moz-focus-inner,
input::-moz-focus-inner {
  padding: 0;
  border: 0;
}
input[type="button"],
input[type="submit"],
input[type="reset"],
input[type="file"]::-webkit-file-upload-button,
input[type="search"]::-webkit-search-cancel-button {
  -webkit-appearance: none;
  appearance: none;
}
mark {
  background-color: rgba(0, 0, 0, 0);
}
a,
ins,
s,
u,
del {
  text-decoration: none;
}
a,
img {
  -webkit-touch-callout: none;
}
a {
  color: #00afc7;
}
.g-clear::after,
.g-mod::after {
  display: block;
  overflow: hidden;
  clear: both;
  height: 0;
  content: " ";
}
@font-face {
  font-family: yofont;
  src: url(//ss.qunarzz.com/yo/font/1.0.3/yofont.woff) format("woff"), url(//ss.qunarzz.com/yo/font/1.0.3/yofont.ttf) format("truetype");
}
.yo-ico {
  font-family: yofont !important;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  vertical-align: middle;
}
.backheader {
  width: 100%;
  height: 0.5rem;
}
.backheader div.topTitle {
    width: 100%;
    height: 100%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
        justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
        align-items: center;
    padding: 5px;
}
.backheader div.topTitle img {
      width: 100%;
      height: auto;
}

